<template>
  <div class="material-container w-full h-full p-10 overflow-y-auto">
    <el-tabs v-model="activeTab">
      <el-tab-pane v-for="item in tabs" :key="item.id" :label="item.name" :name="item.id">
        <el-card class="mt-3 ml-2">
          <template #default>
            <div class="flex gap-[20px] text-[14px] items-center justify-between flex-wrap">
              <div class="flex gap-[12px] items-center">
                <span>筛选项:</span>
                <span>日期:</span>
                <el-date-picker
                  v-model="searchForm.date"
                  type="daterange"
                  unlink-panels
                  start-placeholder="开始日期"
                  end-placeholder="截止日期"
                  :shortcuts="shortcuts"
                  value-format="YYYY-MM-DD"
                  :disabledDate="disabledDate"
                />
                <el-button
                  type="primary"
                  @click="
                    () => {
                      renderDataRef.resetData()
                      renderDataRef.getData(true, getDateParam(searchForm.date))
                    }
                  "
                  >筛选</el-button
                >
              </div>
              <template v-if="item.id === 2">
                <div class="flex gap-[12px] items-center">
                  <span>sku:</span>
                  <el-input
                    v-model="skuInput"
                    style="width: 250px"
                    placeholder="请输入sku"
                    clearable
                    @change="handleSkuInputChange"
                  />
                  <el-button type="primary" @click="handleSkuRun" :disabled="!skuInput"
                    >运行</el-button
                  >
                </div>
              </template>
              <template v-if="item.id === 3">
                <div class="flex gap-[12px] items-center">
                  <span>平台:</span>
                  <el-checkbox-group v-model="checkList">
                    <el-checkbox label="抖音" :value="3" />
                    <!-- <el-checkbox label="快手" :value="4" /> -->
                    <el-checkbox label="小红书" :value="2" />
                  </el-checkbox-group>
                  <span class="ml-2">关键词:</span>
                  <el-input
                    v-model="keyInput"
                    @keyup.enter="handleKeyRun"
                    style="width: 240px"
                    clearable
                    placeholder="请输入"
                  />
                  <el-button type="primary" @click="handleKeyRun" :disabled="!keyInput"
                    >运行</el-button
                  >
                </div>
              </template>
            </div>
          </template>
        </el-card>
      </el-tab-pane>
      <RenderData ref="renderDataRef" @handleSelect="handleSelect" :type="activeTab" />
    </el-tabs>
    <div class="selectBox" v-show="selectedList.length">
      <div class="flex items-center gap-[12px] justify-end mr-[20px]">
        <div>
          已选择
          <span class="text-[#61f11c] font-semibold text-[16px]">{{ selectedList.length }}</span>
          项
        </div>
        <el-button
          type="warning"
          link
          :disabled="!!!selectedList.length"
          @click="
            () => {
              selectedList = []
              renderDataRef.clearSelected()
            }
          "
        >
          取消选择
        </el-button>
        <el-button type="primary" @click="handleDownload" :disabled="!!!selectedList.length"
          >打包下载</el-button
        >
        <el-button v-if="activeTab === 1" :disabled="!!!selectedList.length" @click="transferSave"
          >保存至小说快剪</el-button
        >
        <el-button
          v-if="activeTab === 2"
          :disabled="!!!selectedList.length"
          :loading="transferLoading"
          @click="transferToSmartMixLibrary"
          >保存至智能混剪</el-button
        >
      </div>
    </div>
  </div>
</template>

<script setup>
import { h, reactive, ref, watch } from 'vue'
import RenderData from './RenderData.vue'
import { ElMessage, ElMessageBox } from 'element-plus'

const tabs = [
  {
    id: 1,
    name: '解压视频'
  },
  {
    id: 2,
    name: '京东SKU图片视频获取'
  },
  {
    id: 3,
    name: '关键词获取'
  }
]
const activeTab = ref(tabs[0].id)
const skuInput = ref('')
const keyInput = ref('')
const selectedList = ref([])
const checkList = ref([2, 3])
const renderDataRef = ref()
const skuList = ref([])
const searchForm = reactive({
  date: ''
})
const shortcuts = [
  {
    text: '今天',
    value: () => {
      const end = new Date()
      const start = new Date()
      return [start, end]
    }
  },
  {
    text: '上周',
    value: () => {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
      return [start, end]
    }
  },
  {
    text: '上月',
    value: () => {
      const end = new Date()
      const start = new Date()
      start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
      return [start, end]
    }
  }
]
const disabledDate = (time) => {
  return time.getTime() > Date.now()
}

const handleSelect = (list) => {
  selectedList.value = list
}
const handleDownload = async () => {
  console.log(selectedList.value)
  const urls = selectedList.value.map((el) => el.url)
  urls.forEach((url, index) => {
    const a = document.createElement('a')
    a.href = url
    a.download = true
    a.style.display = 'none'
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)

    // 避免浏览器阻止连续弹出
    setTimeout(() => {}, 100 * index)
  })
}

const handleSkuInputChange = () => {
  let text = skuInput.value.trim()
  if (text.length === 0) {
    skuList.value = []
    return
  }
  skuList.value = text.match(/\d+(?:\.\d+)?/g) || []
}
const handleSkuRun = () => {
  ElMessageBox.confirm(h('div', `识别到有效sku ${skuList.value.length} 条, 是否确认？`), '提示', {
    confirmButtonText: '确定',
    cancelButtonText: '取消'
  })
    .then(() => {
      renderDataRef.value.RunJdData({
        sku_id: skuList.value.join(',')
      })
    })
    .catch(() => {})
}
const handleKeyRun = () => {
  if (checkList.value.length) {
    renderDataRef.value.RunKeyData({
      key_word: keyInput.value,
      platform: checkList.value.join(',')
    })
  } else {
    ElMessage.warning('平台至少选择一个！')
  }
}
const getDateParam = () => {
  if (searchForm.date && searchForm.date.length) {
    return {
      start_date: searchForm.date[0],
      end_date: searchForm.date[1]
    }
  }
  return null
}
const transferSave = () => {
  console.log(selectedList.value)
  ElMessage.warning('暂未开发！')
}
const transferLoading = ref(false)
const transferToSmartMixLibrary = () => {
  // 转存到智能混剪素材库
  if (renderDataRef.value) {
    transferLoading.value = true
    renderDataRef.value.transferToSmartMixLibrary().finally(() => {
      transferLoading.value = false
    })
  }
}
watch(activeTab, () => {
  skuInput.value = ''
  keyInput.value = ''
  skuList.value = []
  searchForm.date = null
})
</script>

<style scoped lang="scss">
.material-container {
  padding: 20px;
  font-size: 14px;
  position: relative;
  overflow: hidden;
}
.selectBox {
  position: absolute;
  bottom: 0;
  height: 50px;
  line-height: 50px;
  left: 0;
  z-index: 8;
  width: 100%;
  background: linear-gradient(135deg, rgb(144, 150, 255), rgb(133, 200, 255));
  box-shadow: 0 4px 12px rgba(116, 189, 255, 0.3);
  color: white;
}
</style>
