<template>
  <div>
    <el-card v-loading="loading" class="mt-3 ml-2 data-list" element-loading-text="正在加载...">
      <template #default>
        <ul
          v-infinite-scroll="handleScroll"
          :infinite-scroll-distance="10"
          :infinite-scroll-disabled="loadingDisabled"
          style="padding-bottom: 20px"
          v-if="dataList.length"
        >
          <li v-for="(item, j) in dataList" :key="item.id">
            <div class="data-item">
              <div class="flex items-center justify-between text-[18px]">
                <span>{{ item.title }}</span>
                <el-checkbox
                  v-if="item.items && item.items.length"
                  @change="handleSelectAll(item.selectedAll, item)"
                  v-model="item.selectedAll"
                  label="全选"
                />
              </div>
              <template v-if="item.status === 'processing' || item.status === 'completed'">
                <template v-if="item.items && item.items.length">
                  <div class="infinite-list">
                    <div
                      class="card-box flex-auto overflow-hidden"
                      v-for="(el, index) in item.items"
                      :key="el.id"
                    >
                      <div class="text-[16px] font-semibold h-full flex items-center bg-[#bbb]" ref="imageBoxRef">
                        <template v-if="el.type === 'video'">
                          <el-image class="w-[100%] h-fit" :src="el.url" lazy>
                            <template #error>
                              <div
                                class="flex flex-col items-center justify-center gap-[10px]"
                              >
                                <LoadIcon class="w-[26px] h-[26px]" />
                                <p class="text-[12px] text-[#fff]">图片加载中...</p>
                              </div>
                            </template>
                          </el-image>
                        </template>
                        <el-image v-else class="w-[100%]" alt="加载失败" :src="el.url" lazy />
                      </div>
                      <div
                        class="operation"
                        @click="
                          (e) => {
                            if (e.target.localName === 'div') {
                              el.selected = !el.selected
                            }
                            handleCheckChange(el, el.selected, item)
                          }
                        "
                      >
                        <div class="operation-icon" @click="(e) => view(e, item, index, el.id)">
                          <el-icon :size="20"><View /></el-icon>
                          <div>{{ el.type === 'video' ? '查看视频' : '查看图片' }}</div>
                        </div>
                      </div>
                      <div :class="['operation-select', { 'operation-selected': el.selected }]">
                        <el-checkbox
                          @change="handleCheckChange(el, el.selected, item)"
                          v-model="el.selected"
                        />
                      </div>
                      <div class="video-info flex justify-between items-center">
                        <span v-show="el.duration">{{ formatSecondsToTime(el.duration) }}</span>
                        <div
                          class="flex gap-[3px] items-center"
                          style="color: var(--el-color-danger-light-5)"
                          v-show="el.like"
                        >
                          <span>{{ formatLike(el.like) }}</span>
                          <svg
                            class="icon"
                            xmlns="http://www.w3.org/2000/svg"
                            width="16"
                            height="16"
                            viewBox="0 0 24 24"
                          >
                            <path
                              d="M20 8h-5.612l1.123-3.367c.202-.608.1-1.282-.275-1.802S14.253 2 13.612 2H12c-.297 0-.578.132-.769.36L6.531 8H4c-1.103 0-2 .897-2 2v9c0 1.103.897 2 2 2h13.307a2.01 2.01 0 0 0 1.873-1.298l2.757-7.351A1 1 0 0 0 22 12v-2c0-1.103-.897-2-2-2zM4 10h2v9H4v-9zm16 1.819L17.307 19H8V9.362L12.468 4h1.146l-1.562 4.683A.998.998 0 0 0 13 10h7v1.819z"
                            ></path>
                          </svg>
                        </div>
                        <span v-show="el.size">{{ el.size }}</span>
                      </div>
                      <div class="tip" v-show="el.type === 'video'">
                        <span>{{ el.orientation === 'portrait' ? '竖屏' : '横屏' }}</span>
                      </div>
                    </div>
                  </div>
                </template>
                <el-result
                  icon="success"
                  v-if="item.status === 'processing'"
                  style="background-color: #f8f8f8"
                >
                  <template #title>
                    <span>正在处理...</span>
                  </template>
                  <template #icon>
                    <LoadIcon class="w-[26px] h-[26px]" />
                  </template>
                </el-result>
                <el-empty
                  v-if="item.status === 'completed' && (!item.items || !item.items.length)"
                  description="暂无数据"
                />
              </template>
              <div v-else>
                <el-result icon="error" title="抱歉，任务生成失败" :sub-title="item.error_message">
                  <template #extra>
                    <!-- <el-button type="primary">Back</el-button> -->
                  </template>
                </el-result>
              </div>
            </div>
            <el-divider v-if="j !== dataList.length - 1" />
          </li>
          <li
            class="mt-3"
            v-loading="pageage.dropLoading"
            element-loading-text="加载中..."
            :element-loading-spinner="loadingSvg"
          ></li>
          <li v-if="pageage.noMore" class="text-center text-[12px] text-gray-400">没有更多啦~</li>
        </ul>
        <el-empty v-else description="暂无数据" />
      </template>
    </el-card>
    <VideoPlayerModal ref="VideoPlayerModalRef" />
    <el-image-viewer
      v-if="showPreview"
      :url-list="imageList"
      :initial-index="initialIndex"
      :infinite="false"
      @close="previewImageClose"
    />
  </div>
</template>
<script setup>
import { ref, watch, reactive, computed, onBeforeUnmount } from 'vue'
import { View } from '@element-plus/icons-vue'
import VideoPlayerModal from './VideoPlayerModal.vue'
import * as _ from 'lodash'
import {
  getTaskListJD,
  executeJDScript,
  executeKeyScript,
  uploadMaterial
} from '@/service/getMaterials'
import { formatSecondsToTime } from '@/utils/utils'
import { ElMessage } from 'element-plus'
import dayjs from 'dayjs'

// 缓存视频
const videoMap = new Map()
const loadingSvg = `<svg  xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1024 1024"><path fill="var(--el-color-primary)" d="M512 64a32 32 0 0 1 32 32v192a32 32 0 0 1-64 0V96a32 32 0 0 1 32-32m0 640a32 32 0 0 1 32 32v192a32 32 0 1 1-64 0V736a32 32 0 0 1 32-32m448-192a32 32 0 0 1-32 32H736a32 32 0 1 1 0-64h192a32 32 0 0 1 32 32m-640 0a32 32 0 0 1-32 32H96a32 32 0 0 1 0-64h192a32 32 0 0 1 32 32M195.2 195.2a32 32 0 0 1 45.248 0L376.32 331.008a32 32 0 0 1-45.248 45.248L195.2 240.448a32 32 0 0 1 0-45.248zm452.544 452.544a32 32 0 0 1 45.248 0L828.8 783.552a32 32 0 0 1-45.248 45.248L647.744 692.992a32 32 0 0 1 0-45.248zM828.8 195.264a32 32 0 0 1 0 45.184L692.992 376.32a32 32 0 0 1-45.248-45.248l135.808-135.808a32 32 0 0 1 45.248 0m-452.544 452.48a32 32 0 0 1 0 45.248L240.448 828.8a32 32 0 0 1-45.248-45.248l135.808-135.808a32 32 0 0 1 45.248 0z"></path></svg>`
const emits = defineEmits(['handleSelect'])
const props = defineProps({
  type: {
    type: Number,
    default: 1
  }
})
const baseUrl = import.meta.env.VITE_API_BASE_URL
const VideoPlayerModalRef = ref()
const selectedList = ref([])
const pageage = reactive({
  page: 1,
  pageSize: 5,
  dropLoading: false,
  noMore: false
})
const dataList = ref([])
const loading = ref(false)
const showPreview = ref(false)
const imageList = ref([])
const initialIndex = ref(0)
const timer = ref(null)
const imageBoxRef = ref(null)
const imageBoxRect = ref(null)
let controller = null
const isProcessing = computed(() => {
  return dataList.value.some((el) => el.status === 'processing')
})

const loadingDisabled = computed(() => pageage.dropLoading || pageage.noMore)
const view = (e, item, index, id) => {
  e.stopPropagation()
  if (item.items[index].type === 'video') {
    const videos = item.items.filter((el) => el.type === 'video')
    const current = videos.findIndex((el) => el.id === id)
    VideoPlayerModalRef.value.open(videos, current, item.title)
  } else {
    const images = item.items.filter((el) => el.type === 'image')
    imageList.value = images.map((el) => el.url)
    initialIndex.value = images.findIndex((el) => el.id === id)
    showPreview.value = true
  }
}

const handleCheckChange = (el, val, item) => {
  if (val) {
    selectedList.value.push({
      id: el.id,
      url: el.viewUrl
    })
  } else {
    selectedList.value = selectedList.value.filter((item) => item.id !== el.id)
    item.selectedAll = false
  }
  if (item.items.every((e) => !!selectedList.value.find((i) => i.id === e.id))) {
    item.selectedAll = true
  }
}

const handleSelectAll = (val, item) => {
  if (item.items && item.items.length) {
    if (val) {
      item.items.forEach((e) => {
        if (!e.selected) {
          selectedList.value.push({
            id: e.id,
            url: e.viewUrl
          })
        }
        e.selected = true
      })
    } else {
      item.selectedAll = false
      item.items.forEach((e) => {
        e.selected = false
        selectedList.value = selectedList.value.filter((item) => item.id !== e.id)
      })
    }
  }
}

const clearSelected = () => {
  selectedList.value = []
  dataList.value.forEach((item) => {
    handleSelectAll(false, item)
  })
}

const handleScroll = _.debounce(async () => {
  pageage.dropLoading = true
  pageage.page++
  await getData(false)
  pageage.dropLoading = false
}, 100)

const getData = async (showLoading = true, params) => {
  console.log('执行啦')
  if (showLoading) {
    loading.value = true
  }
  if (controller) {
    controller.abort()
  }
  controller = new AbortController()
  let data = []
  switch (props.type) {
    case 1:
      data = await getJieYaData(params, {
        signal: controller.signal
      })
      break
    case 2:
      data = await getJdData(params, {
        signal: controller.signal
      })
      break
    case 3:
      data = await getKeyData(params, {
        signal: controller.signal
      })
      break
    default:
      break
  }
  if (data.length) {
    if (isProcessing.value || !diffData(dataList.value, data)) {
      dataList.value = await formatData([...data])
    } else {
      dataList.value = await formatData([...dataList.value, ...data])
    }
    console.log(dataList.value, '数据')
  } else {
    pageage.noMore = true
  }
  loading.value = false
  if (isProcessing.value && !timer.value) {
    timer.value = setTimeout(() => {
      pageage.page = 1
      getData(false)
      clearTimeout(timer.value)
      timer.value = null
    }, 3000)
  }
  return data
}
const getJieYaData = async (params, option) => {
  const data = await getTaskListJD({ ...pageage, ...params, task_type: 2 }, option)
  return data.data.list
}
const getJdData = async (params, option) => {
  const data = await getTaskListJD({ ...pageage, ...params, task_type: 1 }, option)
  return data.data.list
}
const RunJdData = async (params) => {
  const data = await executeJDScript(params)
  ElMessage.success(data.data.message || '任务已提交')
  if (data.data.message && data.data.message.includes('已存在')) {
    return
  }
  resetData()
  getData()
}
const getKeyData = async (params, option) => {
  const data = await getTaskListJD({ ...pageage, ...params, task_type: 3 }, option)
  return data.data.list
}
const RunKeyData = async (params) => {
  const data = await executeKeyScript(params)
  ElMessage.success(data.data.message || '任务已提交')
  if (data.data.message && data.data.message.includes('已存在')) {
    return
  }
  resetData()
  getData()
}
const formatLike = (num) => {
  if (num > 99999999) {
    return (num / 100000000).toFixed(2) + '亿'
  } else if (num > 9999) {
    return (num / 10000).toFixed(2) + '万'
  }
  return num
}
// 组合数据
const formatData = async (arr) => {
  const getDataTemp = async (data, i, j) => {
    if (data.type === 'video') {
      const cacheData = videoMap.get(data.url)
      if (cacheData) {
        data.url = cacheData.url
        data.duration = cacheData.duration
      } else {
        generateCoverFromVideo(data.url, i, j, props.type)
        data.url = ''
        data.duration = ''
      }
    }
    return data
  }

  // 使用 Promise.all 和 map 保持顺序
  const formattedArr = await Promise.all(
    arr.map(async (e, i) => {
      const formattedItem = { ...e } // 创建副本避免修改原对象
      if (props.type == 1) {
        formattedItem.title = dayjs(formattedItem.create_time).format('YYYY-MM-DD')
      } else if (props.type == 2) {
        if (formattedItem.product_info) {
          formattedItem.title = `sku: ${formattedItem.product_info.sku_id}   标题: ${
            formattedItem.product_info.product_short_name || '-'
          }`
        } else {
          formattedItem.title = `sku: ${formattedItem.post_id}   标题: 任务生成失败`
          formattedItem.errorMsg = `${formattedItem.error_message || '接口错误'}`
        }
      } else if (props.type == 3) {
        formattedItem.title = `关键词: ${formattedItem.post_id}   时间: ${dayjs(
          formattedItem.create_time
        ).format('YYYY-MM-DD')}`
      }

      if (formattedItem.items && formattedItem.items.length) {
        // 处理子项
        formattedItem.items = await Promise.all(
          formattedItem.items.map(async (el, j) => {
            const data = {
              id: el.id,
              name: el.file_name,
              type: el.file_type,
              url: baseUrl + el.file_url,
              viewUrl: baseUrl + el.file_url,
              description: null,
              size: el.size_mb,
              like: el.like_count,
              orientation: el.video_orientation
            }
            if (selectedList.value && selectedList.value.length) {
              data.selected = selectedList.value.some((line) => line.id === el.id)
            }
            return await getDataTemp(data, i, j)
          })
        )
        if (
          formattedItem.items.every((x) => !!selectedList.value.find((line) => line.id === x.id))
        ) {
          formattedItem.selectedAll = true
        } else {
          formattedItem.selectedAll = false
        }
      }
      return formattedItem
    })
  )

  return formattedArr
}
// 获取视频封面
const generateCoverFromVideo = (url, i, j, type) => {
  const obj = {
    duration: null,
    url: null
  }
  const video = document.createElement('video')
  video.crossOrigin = 'anonymous' // 处理跨域问题
  video.preload = 'metadata'
  video.src = url

  video.addEventListener('loadeddata', () => {
    if (props.type === type) {
      // 跳转到第一帧
      video.currentTime = 0.1
      obj.duration = video.duration
      if (dataList.value[i] && dataList.value[i].items[j]) {
        dataList.value[i].items[j].duration = video.duration
      }
    }
  })

  video.addEventListener('seeked', () => {
    if (props.type === type) {
      const canvas = document.createElement('canvas')
      if (imageBoxRect.value) {
        canvas.width = imageBoxRect.value.width
        canvas.height = 280
      } else {
        canvas.width = 180
        canvas.height = 280
      }
      const ctx = canvas.getContext('2d')
      ctx.drawImage(video, 0, 0, canvas.width, canvas.height)
      // 将canvas转为DataURL作为封面
      obj.url = canvas.toDataURL('image/jpeg')
      if (dataList.value[i] && dataList.value[i].items[j]) {
        dataList.value[i].items[j].url = obj.url
      }
      videoMap.set(url, obj)
    }
  })
}
const previewImageClose = () => {
  showPreview.value = false
  imageList.value = []
  initialIndex.value = 0
}

const resetData = () => {
  dataList.value = []
  isProcessing.value = false
  pageage.dropLoading = false
  pageage.page = 1
  pageage.noMore = false
  clearTimeout(timer.value)
  timer.value = null
}
// 判断数据是否相同
const diffData = (arr1, arr2) => {
  if (arr1.length !== arr2.length) {
    return false
  }
  const length = arr1.length
  for (let i = 0; i < length; i++) {
    const item1 = arr1[i]
    const item2 = arr2[i]
    if (item1.status !== item2.status || item1.id !== item2.id) {
      return false
    }
  }
  return true
}
// 保存到智能混剪
const transferToSmartMixLibrary = async () => {
  const params = {
    item_ids: selectedList.value.map((item) => item.id).join(',')
  }
  try {
    const res = await uploadMaterial(params)
    if (res.code === 0) {
      ElMessage.success('保存成功')
    } else {
      ElMessage.error(res.message || '保存失败')
    }
  } catch (error) {
    ElMessage.error(error || '保存失败')
  }
}
// 复制
onBeforeUnmount(() => {
  if (timer.value) {
    clearTimeout(timer.value)
  }
})
watch(
  () => selectedList.value.length,
  () => {
    emits('handleSelect', selectedList.value)
  }
)
watch(
  () => props.type,
  () => {
    clearSelected()
    resetData()
    getData()
  },
  {
    immediate: true
  }
)

watch(imageBoxRef, () => {
  if (imageBoxRef.value && imageBoxRef.value[0]) {
    const rect = imageBoxRef.value[0].getBoundingClientRect()
    imageBoxRect.value = {
      width: rect.width,
      heigth: rect.heigth
    }
  }
})

defineExpose({
  clearSelected,
  getData,
  RunJdData,
  resetData,
  dataList,
  RunKeyData,
  transferToSmartMixLibrary
})
</script>
<style lang="scss" scoped>
:deep(.el-loading-spinner) {
  .circular {
    width: 24px;
  }
  .el-loading-text {
    font-size: 12px;
    margin-top: 0;
  }
}
.data-list {
  overflow-y: auto;
  max-height: calc(100vh - 260px);
  .data-item {
    padding: 0 20px;
    .operation-selected {
      display: block;
    }
  }
  .operation {
    display: none;
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 0.75rem;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.2);
    color: #fff;
    font-size: 12px;
    text-align: center;
    .operation-icon {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      cursor: pointer;
    }
  }
  .operation-select {
    display: none;
    position: absolute;
    right: 10px;
    top: 0px;
  }
  .infinite-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(166px, 1fr));
    gap: 16px;
    padding: 10px 0;
  }
  .card-box:hover {
    border-color: transparent;
    box-shadow: 0 0px 3px 3px rgba(0, 0, 0, 0.09), -1px 0px 6px 6px rgba(0, 0, 0, 0.09),
      5px 1px 5px 0px rgba(0, 0, 0, 0.12);
    & .operation,
    .operation-select {
      display: block;
    }
  }
  .card-box {
    background-color: #c7bbbb;
    width: 100%;
    height: 280px;
    border-radius: 0.75rem;
    position: relative;
    .video-info {
      position: absolute;
      width: 100%;
      bottom: 8px;
      left: 0;
      color: #fff;
      font-size: 12px;
      padding: 0 8px;
      .icon {
        fill: var(--el-color-danger-light-5);
      }
    }
    .tip {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 20px;
      background-image: linear-gradient(45deg, #fa8067 0%, #f3bb6c 51%, #f1826c 100%);
      transform: rotate(-45deg) translateY(-150%) translateX(-30%);
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      letter-spacing: 0.1em;
      text-transform: uppercase;
      box-shadow: 0 5px 10px rgba(0, 0, 0, 0.23);
      font-size: 12px;
    }
  }
}
</style>
