import request from '../utils/axios'

// 爬取京东素材
export const executeJDScript = async (params) => {
  return request({
    url: '/material/fetch/jd_material',
    method: 'get',
    showToast: true,
    params
  })
}

// 爬取京东的任务列表
export const getTaskListJD = async (params, option = {}) => {
  return request({
    url: '/material/fetch/jd/list',
    method: 'get',
    showToast: true,
    params,
    ...option
  })
}

// 关键词的任务列表
export const executeKeyScript = async (params) => {
  return request({
    url: '/material/fetch/keyword_material',
    method: 'get',
    showToast: true,
    params
  })
}

// 京东素材上传至素材库
export const uploadMaterial = async (params) => {
  return request({
    url: '/material/fetch/material_lib/upload',
    method: 'get',
    showToast: true,
    params
  })
}
