<template>
    <el-dialog v-model="dialogVisible" :title="title" width="1000" :before-close="handleClose">
        <!-- 左右布局 -->
        <div class="video-container">
            <!-- 左侧视频列表 -->
            <el-card style="border-radius: 12px;width: 400px;">
                <div class="video-list">
                    <div :class="['video-item', {active: currentIndex === index}]" v-for="(video, index) in videoList" :key="index" @click="play(index)">
                        <div class="thumbnail" :style="{background: `url(${video.url}) 100%/100%`}">
                            <div class="loading" v-if="currentIndex === index">
                                <div class="load"></div>
                                <div class="load"></div>
                                <div class="load"></div>
                                <div class="load"></div>
                                <div class="load"></div>
                            </div>
                        </div>
                        <div class="video-info">
                            <span>{{ video.name }}</span>
                            <span>{{ video.duration ? formatSecondsToTime(video.duration) : '暂无' }}</span>
                        </div>
                    </div>
                </div>
            </el-card>
            <!-- 右侧视频播放区域 -->
            <div class="video-player" :key="currentVideoUrl">
                <video controls ref="videoRef">
                    <source :src="currentVideoUrl" type="video/mp4" />
                    您的浏览器不支持 video 标签。
                </video>
            </div>
        </div>
    </el-dialog>
</template>

<script setup>
import { ref } from 'vue'
import { formatSecondsToTime } from '@/utils/utils'

// 视频列表数据
const videoList = ref([])
const videoRef = ref()

// 当前播放的视频 URL
const currentVideoUrl = ref('')
const currentIndex = ref(0);

// 弹窗显示状态
const dialogVisible = ref(false)
const title = ref('')

// 打开弹窗方法
const open = (urlList, current, t) => {
    console.log('open', urlList, current, t)
    dialogVisible.value = true
	videoList.value = urlList
    currentVideoUrl.value = urlList[current].viewUrl
	currentIndex.value = current
	if (t) {
		title.value = '预览视频' + '-' + t
	} else {
		title.value = '预览视频'
	}
}

// 关闭弹窗方法
const handleClose = () => {
    dialogVisible.value = false
	videoRef.value?.pause()
}

const play = (index) => {
    console.log('play')
	currentIndex.value = index;
	currentVideoUrl.value = videoList.value[index].viewUrl
}

// 定义暴露的方法
defineExpose({
    open
})
</script>

<style scoped lang="scss">
/* 样式 */
.video-container {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    .loading {
        width: 18px;
        height: 18px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .load {
        background-color: #fff;
        width: 2px;
        height: 12px;
        border-radius: 1px;
        animation: playing 1s -0.6s infinite cubic-bezier(0.85, 0.25, 0.37, 0.85);
        animation-fill-mode: both;
    }

    .load:nth-child(2),
    .load:nth-child(4) {
        animation-delay: -0.4s !important;
    }

    .load:nth-child(1),
    .load:nth-child(5) {
        animation-delay: -0.2s !important;
    }

    @keyframes playing {
        0% {
            transform: scaleY(1);
        }

        50% {
            transform: scaleY(0.4);
        }

        to {
            transform: scaleY(1);
        }
    }
}

.video-list {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-height: 600px;
    overflow-y: auto;
}

.video-item {
    display: flex;
    align-items: center;
    gap: 10px;
	cursor: pointer;
	border-radius: 4px;
	padding: 0 5px;
	&:hover {
		background-color: #f5f5f5;
	}
	&.active {
		background-color: #d8d8d8;
	}
}

.thumbnail {
    width: 50px;
	min-width: 50px;
    height: 50px;
    border-radius: 5px; /* 缩略图圆角 */
    background-color: pink;
    display: flex;
    align-items: center;
    justify-content: center;
}

.video-info {
    display: flex;
    flex-direction: column;
}

.video-player {
    border-radius: 10px; /* 添加圆角 */
	flex: 1;
    video {
        border-radius: 12px;
		width: 100%;
        height: 600px;
    }
}

.video-details {
    background-color: #f0f0f0;
    padding: 10px;
}
</style>
